<template>
  <div class="container">
    <div v-if="recipe">
      <div class="recipe-header mb-2 mt-3 mb-4">
        <h1>{{ recipe.title }}</h1>
        <img :src="recipe.image" class="center" />
      </div>
      <div class="recipe-body">
        <div class="wrapper">
          <div class="wrapped">
            <div class="mb-3">
              <div>Ready in {{ recipe.readyInMinutes }} minutes</div>
              <div>Likes: {{ recipe.aggregateLikes }} likes</div>
              <div>Servings: {{ recipe.servings }}</div>
            </div>
            <div style="display: flex;">
              <p v-if="recipe.vegetarian" style="margin-right: 10px;"><img src="data:image/png;base64,"></p>
              <p v-if="recipe.vegan" style="margin-right: 10px;"><img src="data:image/png;base64,"></p>
              <p v-if="recipe.glutenFree" style="margin-right: 10px;"><img src="data:image/png;base64,"></p>
              <p v-if="!recipe.glutenFree"><img src="data:image/png;base64,"></p>
              <b-button variant="link" class="text-decoration-none mb-2" @click="toggleFav">
                <img v-if="!isFav" src="data:image/png;base64," alt="Image 1">
                <img v-else src="data:image/png;base64," alt="Image 2">
              </b-button>
            </div> 
            Ingredients:
            <ul>
              <li
                v-for="(r, index) in recipe.extendedIngredients"
                :key="index + '_' + r.id"
              >
                {{ r.original }}
              </li>
            </ul>
          </div>
          <div class="wrapped">
            Instructions:
            <ol>
              <li v-for="s in recipe._instructions" :key="s.number">
                {{ s.step }}
              </li>
            </ol>
          </div>
        </div>
      </div>
      <!-- <pre>
      {{ $route.params }}
      {{ recipe }}
    </pre
      > -->
    </div>
  </div>
</template>
<script>
import { mockGetRecipeFullDetails } from "../services/recipes.js";
import { mockAddFavorite } from "../services/user.js";
export default {
  name: "RecipeViewPage",
  data() {
    return {
      recipe: null,
      isFav: localStorage.getItem(`favorite_${this.recipe.id}`) === 'true',
      message: ""
    };
  },
  async created() {
    try {
      let response;
      // response = this.$route.params.response;

      try {
        // response = await this.axios.get(
        //   this.$root.store.server_domain + "/recipes/" + this.$route.params.recipeId,
        //   {
        //     withCredentials: true
        //   }
        // );

        response = mockGetRecipeFullDetails(this.$route.params.recipeId);

        // console.log("response.status", response.status);
        if (response.status !== 200) this.$router.replace("/NotFound");
      } catch (error) {
        console.log("error.response.status", error.response.status);
        this.$router.replace("/NotFound");
        return;
      }

      let {
        analyzedInstructions,
        instructions,
        extendedIngredients,
        aggregateLikes,
        readyInMinutes,
        image,
        title,
        servings,
        vegetarian,
        vegan,
        glutenFree
      } = response.data.recipe;

           // Concatenate name with each step in analyzedInstructions
           let _instructions = analyzedInstructions.flatMap(instruction => {
        let sectionName = instruction.name ? `${instruction.name}` : '';
        return instruction.steps.map(step => ({
          ...step,
          step: `${sectionName}${step.step}`
        }));
      });

      let _recipe = {
        instructions: analyzedInstructions,
        _instructions,
        analyzedInstructions,
        extendedIngredients,
        aggregateLikes,
        readyInMinutes,
        image,
        title,
        servings,
        vegetarian,
        vegan,
        glutenFree
      };

      this.recipe = _recipe;
    } catch (error) {
      console.log(error);
    }
  },
  methods: {
  toggleFav(event) {
      event.stopPropagation();
      this.isFav = !this.isFav;
      localStorage.setItem(`favorite_${this.recipe.id}`, this.isFav.toString());
      if (this.isFav){
        if (mockAddFavorite(this.recipe.id).status === 200){
          this.message = mockAddFavorite(this.recipe.id).response.data.message;
        }
      }
      else{
        this.message = "The Recipe successfully removed from favorites"
      }
      this.$root.toast("",this.message, "Light ");
    },
  }
};
</script>
<style scoped>

.wrapper {
  display: flex;
}
.wrapped {
  width: 50%;
}
.recipe-header {
  text-align: center; /* Ensures the header text is centered */
}
.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}
/* .recipe-header{

} */
</style>
